@import '../../styles/variable.scss';

.chartMenu {
  width: 100vw;
  height: 40px;
  background: #1A1A1A;
  color: rgba(255, 26, 104, 1);
}

.chartMenu p {
  padding: 10px;
  font-size: 20px;
}

.chartCard {
  width: 100vw;
  height: calc(100vh - 40px);
  background: rgba(255, 26, 104, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
}

.chartBox {
  width: 700px;
  padding: 20px;
  border-radius: $radius;
  border: solid 3px rgba(255, 26, 104, 1);
  background: white;
}

h3.head{
  text-align: center;
  margin-top: $size;
}